@keyframes progress {
  0% {
    stroke-dasharray: 0 100; } }

@keyframes slideInBelow {
  0% {
    transform: translateY(60px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes slideInLeft {
  0% {
    transform: translateX(-50px);
    opacity: 0; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

@keyframes slideInRight {
  0% {
    transform: translateX(50px);
    opacity: 0; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

@keyframes slideInTop {
  0% {
    transform: translateY(-70px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes rotateOut {
  0% {
    opacity: 1;
    transform: rotateY(90deg); }
  50% {
    opacity: 0.5;
    transform: rotateY(0deg); }
  100% {
    display: none;
    opacity: 0;
    transform: rotateY(90deg); } }

.contacts-container {
  position: relative;
  width: 100%;
  display: flex;
  flex-wrap: wrap; }
  .contacts-container > * {
    flex-basis: 33%;
    padding-top: 50px; }
  .contacts-container img {
    width: 45px;
    font-weight: 200;
    margin: 0 20px;
    cursor: pointer; }
  .contacts-container .copy-right {
    position: relative;
    flex-basis: 100%;
    padding-top: 0;
    margin-top: 30px;
    height: 35px; }
    .contacts-container .copy-right .content {
      position: relative;
      width: 100%;
      height: 100%;
      padding-right: 20px;
      border-top: 1px solid #E6EAEA;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-size: 12px;
      line-height: 20px;
      color: #757575;
      font-weight: 100; }
      .contacts-container .copy-right .content img {
        height: 12px;
        margin: 0; }
  .contacts-container .coordonne {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-weight: 200;
    font-size: 20px;
    color: #181818; }
  .contacts-container .social-network {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    color: #181818; }
    .contacts-container .social-network .main-title {
      position: relative;
      font-weight: 200;
      font-size: 37px; }
      .contacts-container .social-network .main-title:before {
        content: '';
        position: absolute;
        left: -40px;
        top: 28px;
        width: 20px;
        height: 1px;
        background: #181818; }
      .contacts-container .social-network .main-title:after {
        content: '';
        position: absolute;
        right: -40px;
        top: 28px;
        width: 20px;
        height: 1px;
        background: #181818; }
    .contacts-container .social-network .logos {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 40px; }
    .contacts-container .social-network .qc-logo {
      position: absolute;
      width: 80px;
      top: 150px; }
  .contacts-container .go-up {
    display: flex;
    justify-content: center;
    align-items: flex-start; }
    .contacts-container .go-up span {
      font-weight: 200;
      font-size: 27px;
      color: #181818; }
  .contacts-container .is-rotateOut {
    animation: rotateOut .8s ease-out forwards; }
  .contacts-container .is-hide {
    display: none; }

.wechat-tooltip {
  background: #fff;
  border: 1px solid grey;
  border-radius: 4px;
  padding: 4px;
  opacity: 1; }
  .wechat-tooltip span {
    display: flex; }
    .wechat-tooltip span img {
      width: 160px;
      cursor: pointer; }
