.expo-service-card {
  position: relative;
  top: 0;
  width: 240px;
  padding: 30px 15px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .expo-service-card .card-img {
    position: absolute;
    top: -50px; }
    .expo-service-card .card-img img {
      height: 70px; }
  .expo-service-card .card-title {
    font-size: 15px;
    font-weight: 600;
    margin-bottom: 20px; }
  .expo-service-card .card-dsptn {
    text-align: left; }
    .expo-service-card .card-dsptn .dsptn-item {
      font-size: 14px;
      line-height: 25px; }
