.site-expo-container {
  position: relative;
  width: 100%; }
  .site-expo-container .services {
    display: flex;
    justify-content: center;
    background: #fff; }
  .site-expo-container .aboutus-container {
    width: 100%;
    height: 360px; }
  .site-expo-container .contacts {
    background: #343148ff;
    opacity: .9;
