.accueil {
  position: relative;
  width: 100%;
  height: 700px;
  overflow: hidden; }
  .accueil .carousel-bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: inset 5px 54px 40px 3px rgba(61, 58, 61, 0.98); }
  .accueil .carousel-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.5;
    background-color: #1c1d21; }
  .accueil .carousel-content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
    .accueil .carousel-content .text-wrapper {
      position: absolute;
      bottom: 30%;
      left: 40%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #fff; }
      .accueil .carousel-content .text-wrapper .carousel-title-main {
        opacity: 0;
        font-size: 50px;
        font-weight: 100;
        margin-bottom: 20px;
        animation-delay: 0.2s; }
      .accueil .carousel-content .text-wrapper .carousel-title-sub {
        opacity: 0;
        font-size: 20px;
        font-weight: 100;
        margin-bottom: 40px;
        animation-delay: 0.3s; }
      .accueil .carousel-content .text-wrapper .carousel-btn {
        opacity: 0;
        padding: 0 30px;
        border: 1px solid #fff;
        line-height: 40px;
        animation-delay: 0.4s;
        cursor: pointer; }
  .accueil .slideIn {
    animation: slideInBelow 0.8s ease-out forwards; }
