.expo-services {
  position: relative;
  margin-top: 80px;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .expo-services .service-title {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    opacity: 0; }
    .expo-services .service-title .main-title {
      font-size: 40px;
      font-weight: 300;
      margin-bottom: 10px; }
    .expo-services .service-title .sub-title {
      text-align: left;
      max-width: 700px;
      font-size: 13px;
      color: #5f6368;
      white-space: normal; }
  .expo-services .service-list {
    position: relative;
    margin-top: 100px;
    margin-bottom: 100px;
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    box-shadow: 3px 12px 21px 4px rgba(154, 154, 154, 0.41);
    opacity: 0; }
  .expo-services .slideIn-left {
    animation: slideInLeft 0.8s ease-out forwards; }
  .expo-services .slideIn-right {
    animation: slideInRight 0.8s ease-out forwards; }
