.expertises {
  position: relative;
  width: 100%;
  min-height: 500px; }
  .expertises .bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url(./static/images/carousel2.c174a01.png); }
  .expertises .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #5D5C61;
    opacity: 0.95; }
  .expertises .content {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; }
    .expertises .content .main-title {
      width: 100%;
      font-size: 40px;
      line-height: 200px;
      color: #fff;
      font-weight: 200;
      opacity: 0;
      text-align: left;
      margin-left: 20vw; }
    .expertises .content .main-body {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 100%;
      flex-wrap: wrap; }
  .expertises .slideIn {
    animation: slideInLeft 1.4s ease-out forwards; }
