.expo-header {
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 999; }
  .expo-header.is-absolute {
    position: absolute;
    color: #fff; }
  .expo-header.is-fixed {
    position: fixed;
    color: #fff;
    background: #343148ff;
    animation: slideInTop .2s ease-out forwards; }
  .expo-header-logo img {
    width: 60px; }
  .expo-header-menu {
    display: flex;
    width: 700px;
    justify-content: flex-end;
    align-items: center; }
    .expo-header-menu img {
      cursor: pointer;
      height: 28px;
      width: 28px; }
    .expo-header-menu .menu-item {
      min-width: 90px;
      margin: 0 10px;
      cursor: pointer; }
      .expo-header-menu .menu-item.is-active {
        color: #00b1d2ff; }
