.expertise-circle {
  position: relative;
  margin: 15px 60px; }
  .expertise-circle .circular-chart {
    display: block;
    width: 200px;
    margin-bottom: 10px; }
  .expertise-circle .circle-bg {
    fill: none;
    stroke: #eee;
    stroke-width: 3.8; }
  .expertise-circle .circle {
    stroke: #ff9f00;
    fill: none;
    stroke-width: 2.8;
    stroke-linecap: round; }
  .expertise-circle .onEnter {
    animation: progress 1.4s ease-out forwards; }
  .expertise-circle .percentage {
    fill: #fff;
    font-family: sans-serif;
    font-size: 7px;
    font-weight: 100;
    text-anchor: middle; }
  .expertise-circle .expertise-title {
    font-size: 16px;
    font-weight: 300;
    color: #fff;
    font-family: sans-serif; }
